@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Square+Peg&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    outline: 0;
    list-style: none;
    text-decoration: none;
}

:root {
